@import url(https://fonts.googleapis.com/css2?family=Archivo:ital,wght@1,700&family=Lato:wght@300;400;900&family=MuseoModerno:wght@300;700&family=Noto+Sans+JP:wght@900&family=Rokkitt:wght@300;500&family=Yellowtail&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Dancing+Script:wght@400;700&family=Henny+Penny&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-base-color: black;

/* 
  font-family: 'Cantarell', sans-serif;

  font-family: 'Fira Sans', sans-serif;

  font-family: 'Oxygen', sans-serif;

  font-family: 'Roboto', sans-serif; */


  /* font-family: 'Anton', sans-serif;

  font-family: 'Dancing Script', cursive;

  font-family: 'Henny Penny', cursive;

  font-family: 'Pacifico', cursive; */
  
}
html,
body,
.main {
  margin: 0;
  height: 100%;
  width: 100%;
  background-color: #141414;
  overflow-x: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Global css */

:root {
    --main-red: rgba(211,9,19);
    --main-dark: #141414;
    --main-deep-dark: #141414;
    --main-grey: rgba(153,153,153);
    --main-red-hover: #e50914;
}

* {
    box-sizing: border-box;
}

/* header container */

.header.containers {
    z-index: 0;
}

.navbar-link {
    z-index:999;
}

.tab-icon-door {
    width: 48px;
    height: 48px;
    fill: var(--main-grey);
}
@media(max-width: 900px) {
    .tab-icon-door {
        width: 2.4rem;
        height: 2.4rem;
    }
}
.mdScreen.price {
    width: 100px;
}
.tab-icon-devices {
    width: 130px;
    height: 130px;
    fill: var(--main-grey);
}
@media(max-width: 900px) {
    .tab-icon-devices {
        width: 120px;
        height: 120px;
    }
}

.tab-icon-price {
    width: 48px;
    height: 48px;
    fill: var(--main-grey);
}
@media(max-width: 900px) {
    .tab-icon-price {

    }
}



.tabs {
    padding-top: 16px;

}

.tab-nav-container {
    display: grid;
    justify-content: center;
    align-content: center;
    text-align: center;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;
    padding:0;
    list-style: none;
    color: var(--main-grey);
    margin: 0 auto;
}
@media(min-width:700px) {
    .tab-nav-container {
        margin: 0 11%;
    }
}

ul li:hover {
    color: white;
    cursor: pointer;
}
ul li:hover svg path {
    fill: white;
}

.active {
    border-bottom: 4px solid var(--main-red);
}
.tab-selected {
    color: #fff;
}
.tab-selected svg path {
    fill: white;
}


@media(min-width: 900px) {
    .mdScreen {
        display: none;
    }
}
@media(max-width: 900px) {
    .lgScreen {
        display: none;
    }
}
.main-login-container {
    width: 100%;
    height: 100%;
    position: relative;
    background: radial-gradient(rgba(0,0,0,0), rgba(0,0,0,0.7)), url('https://cdn.pixabay.com/photo/2019/02/17/03/17/sunset-4001481_960_720.jpg') no-repeat;
    background-size: 100% 35% ;
    font-family: "Jura", sans-serif;
}
@media(min-width: 500px) {
    .main-login-container {
        background-size: 100%;
    }
}
.main-login-container::after {
    width: 100%;
    height: 100%;
    position: relative;
    box-shadow: inset 70px 50px 190px #000, inset -70px -50px -190px;
}

.header-top {
    position: relative;
    height: 10rem;
    z-index: 1;   
}
.stop {
    width: 100%;
    height: 46px;
    background-color: black;
}
@media(min-width:990px) {
    .stop {
        height: 56px;
    }
}
.player-wrapper {
    position: relative;
    padding-top: 57%;
}

.react-player {
    margin-top: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
    
}
